<template>
  <div>
    <guaranty-tab @changeTab="handleChangeTab"></guaranty-tab>
    <div class="order-list" v-if="guarantyTabCur===0">
      <div class="page-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
          <li v-for="(item,index) in outlist" class="page-infinite-listitem" :key="index">
            <base-guaranty :guaranty="item"></base-guaranty>
          </li>
        </ul>
        <p v-show="loading" class="page-infinite-loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          加载中...
        </p>
        <p v-show="!loading&&outlist.length" class="noMoreData">没有更多数据了</p>
        <div v-show="!outlist.length" class="no-result-wrapper">
          <no-result :title="'暂无团险保单'" :src="src"
                     :absTop="82"></no-result>
        </div>
      </div>
    </div>
    <div class="order-list" v-if="guarantyTabCur===1">
      <div class="page-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
          <li v-for="(item,index) in guranteeList" class="page-infinite-listitem" :key="index">
            <base-guaranty :guaranty="item"></base-guaranty>
          </li>
        </ul>
        <p v-show="loading" class="page-infinite-loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          加载中...
        </p>
        <p v-show="!loading&&guranteeList.length" class="noMoreData">没有更多数据了</p>
        <div v-show="!guranteeList.length" class="no-result-wrapper">
          <no-result :title="'暂无团险保单'" :src="src"
                     :absTop="82"></no-result>
        </div>
      </div>
    </div>
    <div class="order-list" v-if="guarantyTabCur===2">
      <div class="page-infinite-wrapper" ref="wrapper" :style="{ height: wrapperHeight + 'px' }">
        <ul class="page-infinite-list" v-infinite-scroll="loadMore" infinite-scroll-distance="50">
          <li v-for="(item,index) in allList" class="page-infinite-listitem" :key="index">

            <base-guaranty :guaranty="item"></base-guaranty>
          </li>
        </ul>
        <p v-show="loading" class="page-infinite-loading">
          <mt-spinner type="fading-circle"></mt-spinner>
          加载中...
        </p>
        <p v-show="!loading&&allList.length" class="noMoreData">没有更多数据了</p>
        <div v-show="!allList.length" class="no-result-wrapper">
          <no-result :title="'暂无团险保单'" :src="src"
                     :absTop="82"></no-result>
        </div>
      </div>
    </div>
    <router-view></router-view>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import noResult from '@/components/base/noResult'
import baseGuaranty from '@/components/personalCenter/myGuaranty/BaseGuaranty'
import guarantyTab from '@/components/personalCenter/myGuaranty/guarantyTab'

export default {
  name: 'page-tab-container',
  data () {
    return {
      page: 1,
      limit: 10,
      outlist: [],
      guranteeList: [],
      src: require('@/assets/images/no_group.png'),
      allList: [],
      loading: false,
      wrapperHeight: 0
    }
  },
  mounted () {
    // this.wrapperHeight = document.documentElement.clientHeight - this.$refs.wrapper.getBoundingClientRect().top
    this.getList()
  },
  computed: {
    ...mapState([
      'guarantyTabCur',
      'guarantyTabCurState'
    ])

  },
  methods: {
    handleChangeTab (index) {
      this.getList()
    },
    getList () {
      this.page = 1
      let postData = {
        page: this.page,
        limit: this.limit,
        status: this.guarantyTabCurState
      }
      this.$api.personCenter.getGroupInsuranceList(postData).then((res) => {
        if (res.code === 1) {
          this.loading = true
          // this.lists = res.data.dataGrid.data
          switch (this.guarantyTabCurState) {
            case '2':
              this.outlist = res.data.data
              break
            case '3':
              this.guranteeList = res.data.data
              break
            case '':
              this.allList = res.data.data
              break
          }
          this._checkMore(res.data)
          // $set(this.lists, `status${this.status}`, res.data.dataGrid.data)
        }
      })
    },
    loadMore () {
      if (!this.loading) {
        return
      }
      this.page++
      this.$api.personCenter.getGroupInsuranceList({
        page: this.page,
        limit: this.limit,
        status: this.guarantyTabCurState
        // status: this.status
      }).then(
        function (res) {
          // this.$set(this.lists, `status${this.status}`, res.data.dataGrid.data)\
          if (res.code === 1) {
            switch (this.guarantyTabCurState) {
              case '2':
                this.outlist = this.outlist.concat(res.data.data)
                break
              case '3':
                this.guranteeList = this.guranteeList.concat(res.data.data)
                break
              case '':
                this.allList = this.allList.concat(res.data.data)
                break
            }
          }

          this._checkMore(res.data)
        }.bind(this)
      )
        .catch(function (err) {
        })
    },
    _checkMore (data) {
      if (!data.data.length || (this.page * this.limit) >= data.count) {
        this.loading = false
      }
    }
  },

  components: {
    guarantyTab,
    baseGuaranty,
    noResult
  }
}
</script>

<style lang="scss" scoped>
  @import '@/assets/styles/mixin.scss';
  .page-infinite-loading{
    text-align: center;
  }
  .mint-spinner-fading-circle{
    margin:0 auto;
  }
  .noMoreData{
    margin-top: pxToRem(30px);
    color: #A5A8A8;
    text-align: center;
  }
  .page-infinite-listitem {
    margin-bottom: pxToRem(20px);
  }
</style>
