<template>
  <div>
    <div class="base-item" @click='handleClickToDetail(guaranty)'>
      <div class="orderId ">
        <div class="orderSn ">
          订单编号: {{guaranty.order_sn}}
        </div>
        <div class="orderStatus ">
          {{groupStatus(guaranty.status)}}
        </div>
      </div>
      <div class="order-content">
        <div class="content-text">
          <div class="title-logo">
            <div class="title" v-text="guaranty.plan_name"></div>
            <div class="logo"><img :src='baseSrc+guaranty.brandLogo'></div>
          </div>
          <div class="msg">保单号: {{guaranty.policy_num}}</div>
          <div class="msg">投保人: {{guaranty.companyName}}</div>
          <div class="msg">被保险人: {{userInfo.name}}</div>
          <div class="msg">保障期间: {{guaranty.policy_effective_date}} 至 {{guaranty.policy_expiration_date}}</div>
        </div>
      </div>

    </div>
    <div class="btn-contair" v-if="guaranty.status==3">
      <p class="btn" @click="goClaimApply(guaranty)">申请理赔</p>
    </div>

  </div>
</template>

<script>
import { groupStatus } from '@/assets/js/mixins/base.js'
import { mapGetters } from 'vuex'

export default {
  mixins: [groupStatus],
  props: {
    leftWidth: {
      type: Number
    },
    rightWidth: {
      type: Number,
      default: 260
    },
    guaranty: {
      type: Object,
      default: () => {
        return {}
      }
    }
  },
  data () {
    return {
      baseSrc: '',
      parameters: []
    }
  },
  mounted () {
    this.baseSrc = `${process.env.VUE_APP_RESOURCE_URL}`
  },
  computed: {
    comLeftWidth () {
      return {
        width: this.leftWidth ? this.leftWidth + 'px' : '800px'
      }
    },
    comRightWidth () {
      return {
        width: this.rightWidth ? this.rightWidth + 'px' : '800px'
      }
    },
    ...mapGetters(['userInfo'])
  },
  methods: {
    goClaimApply (item) {
      this.$router.push({
        path: '/claimList/claimApply/',
        query: {
          orderSn: item.order_sn,
          type:'1',
          sn:'',
        }
      })
    },

    handleClickToDetail (item) {
      this.$router.push({
        path: '/myGuaranty/' + item.id,
        query: {
          guarantyId: item.id
        }
      })
    }
  }
}
</script>

<style lang="scss" scoped="scoped">
  @import '@/assets/styles/mixin.scss';
  @import '@/assets/styles/baseList.scss';
  .btn-contair {
    background: #fff;
    height: pxToRem(100px);
    justify-content: flex-end;
    display: flex;
    .btn {
      border: pxToRem(2px) solid #1FD8D2;
      width: pxToRem(150px);
      height: pxToRem(60px);
      line-height: pxToRem(60px);
      text-align: center;
      margin-top: pxToRem(20px);
      border-radius: pxToRem(30px);
      font-size: pxToRem(24px);
      color: $color-active-text;
      background: #fff;
      margin-right: pxToRem(20px);
      &.last-child {
        margin-right: 0;
      }
    }
    .toClaim {
      color: $color-active-text;
    }
  }
</style>
